import carouselItem from '~/components/carousels/items/CarouselItem.vue'
export default {
  name: 'CarouselImgDes',
  components: {
    carouselItem,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    elementsToShow: {
      type: Array, // img: contain path, title: title desc, description: contain description, linkPath: link path
      required: true,
    },
    linkName:{
      type: String,
      required: false,
      default: ''
    },
    numOfCarousel: {
      type: Number,
      required: true,
    },
  },
  data() {
    
    const length = this.elementsToShow.length
    return {
      length,
    }
  },
  computed: {
    amountOfindicatorsNeeded() {
      return this.length
    },
  },
}
